<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="8">
          <b-card-group>
            <b-card no-body class="p-4">
              <b-card-body>
                <b-form @submit="onSubmit" v-if="show">
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-user"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="text"
                      class="form-control"
                      placeholder="Username"
                      v-model="form.username"
                      autocomplete="username email"
                    />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-lock"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="password"
                      class="form-control"
                      placeholder="Password"
                      v-model="form.password"
                      autocomplete="current-password"
                    />
                  </b-input-group>
                  <b-input-group class="mb-4">
                    <b-input-group-prepend>
                      <b-input-group-text>
                        <i class="icon-lock"></i>
                      </b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input
                      type="password"
                      class="form-control"
                      placeholder="Google Autheticator code"
                      v-model="form.google_code"
                    />
                  </b-input-group>
                  <b-row>
                    <b-col cols="12">
                      <b-button
                        v-if="!loading"
                        type="submit"
                        variant="primary"
                        class="px-4 btn-block"
                      >Login</b-button>
                      <b-button
                        v-if="loading"
                        type="submit"
                        variant="primary"
                        class="px-4 btn-block"
                        disabled
                      >
                        <i class="fa fa-spinner fa-spin" /> Loading
                      </b-button>
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
            <b-card no-body class="text-white bg-dark py-5 d-md-down-none" style="width:44%">
              <b-card-body class="text-center d-flex align-items-center justify-content-center">
                <div>
                  <h2>ADMIN</h2>
                </div>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>
import router from "@/router";
import { serverBus } from "@/main";

export default {
  name: "Login",
  data() {
    return {
      loading: false,
      show: true,
      form: {},
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      this.loading = true;
      this.$http
        .post("/api/v1/admin/sessions", this.form)
        .then((response) => {
          this.$store.commit("setCurrentUser", response.data.user);
          this.$store.commit("setAuthToken", response.data.auth_token);
          this.storeLogin(response.data);
          this.loading = false;
        })
        .catch((error) => {
          this.$toasted.show(error.response.data, {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true,
          });
          this.loading = false;
        });
    },
    storeLogin(data) {
      let dashboardRoute = { name: "Dashboard.dashboard" };
      if (data.user.username === "activaciones") dashboardRoute = { name: "Invoices.list" };
      if (data.user.username === "medios") dashboardRoute = { name: "Media.list" };

      router.push(dashboardRoute);
      (async () => {
        await serverBus.$emit(
          "welcomeHome",
          "Welcome back " + data.user.username
        );
      })();
    },
  },
};
</script>
